.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.bar{
margin:2px;
display: flex;
flex-direction: column;
justify-content: flex-start;
align-items: center;
}

.secondary-header{
  align-items:center;
  background-color: #ffb942;
  width: 100%;
}

.btn{
  
  padding: 0.6em;
  margin:1em;
  font-size: 1em;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  background-color: #00ff19;
  border: none;
  border-radius: 5px;
  box-shadow: 0 9px rgb(7, 216, 42);

}
.btn:hover {background-color: #3fe644}

.btn:active {
  background-color: #3bee41;
  box-shadow: 0 5px rgb(7, 216, 42);
  transform: translateY(4px);
}

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  background-color: #3D5368;
  color:aliceblue;
  
}

.headerText {
  font-size: 2.5em;
  /* position: absolute; */
  /* top: 50%;
  left: 30%;
  transform: translate(-50%, -50%); */
}

a{
  text-decoration: none;
}

.button {
  border-radius: 4px;
  background-color: #317abf;
  color: #FFFFFF;
  text-align: center;
  font-size: 1em;
  padding: 0.5em;
  width: fit-content;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button:hover span {
  padding-right: 25px;
}

.button:hover span:after {
  opacity: 1;
  right: 0;
}